import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import NavBar from "./components/NavBar/navBar";

function App() {
  return (
    <Router>
      <NavBar />
      <Switch>
        <Route path="/products" exact>
          <div>Adios</div>
        </Route>
        <Route path="/principal" exact>
          <div>Hola</div>
        </Route>
        <Route path="**">
          <Redirect to="/principal" />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
